import React from 'react'
import imagem from '../images/trabalhista.png'

export default function Trabalhista_atua() {
  return (
    <div className='flex items-center font-serif flex-col bg-color-white'>
        <div className='bg-color-white pt-12 pb-4 '>
            <h1 className='text-color-clicado font-bold text-4xl'>Direito Trabalhista</h1>
        </div>
        <div className=' filter grayscale  hidden lg:block bg-color-white'>
            <img className='w-full' src={imagem}></img>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>O que é direito Trabalhista?</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O Direito Trabalhista é uma área do direito que se concentra nas relações laborais entre empregadores e empregados. Sua principal função é proteger os direitos dos trabalhadores e regulamentar as condições de emprego. Isso inclui questões como contratos de trabalho, salários, horas de trabalho, férias, segurança no trabalho e direitos sindicais.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O objetivo do Direito Trabalhista é garantir que os trabalhadores sejam tratados de maneira justa e que tenham condições de trabalho seguras e adequadas. Além disso, o Direito Trabalhista também estabelece mecanismos para resolver disputas entre empregadores e empregados, garantindo o cumprimento das leis trabalhistas.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Seja para questões salariais, horas extras não pagas, demissões injustas, assédio no local de trabalho ou qualquer outro problema trabalhista, nossa equipe de advogados está preparada para representá-lo com dedicação e competência.</h1>
        </div>
        
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Contrato de Trabalho</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Um contrato de trabalho é um acordo legal que estabelece os termos e condições de emprego entre um empregador e um empregado. Este documento é essencial para formalizar a relação de trabalho, definindo elementos como salário, jornada de trabalho, funções, benefícios, direitos e deveres de ambas as partes. Os contratos de trabalho podem variar em termos de duração, sendo de prazo determinado ou indeterminado, e podem abranger acordos adicionais, como cláusulas de confidencialidade ou não concorrência. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Além disso, é importante observar que os contratos de trabalho devem estar em conformidade com as leis e regulamentações trabalhistas locais e nacionais, visando a proteção dos direitos dos trabalhadores e a garantia de um ambiente de trabalho justo e equitativo.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Se você está enfrentando problemas com seu contrato de trabalho, seja como empregador ou empregado, estamos aqui para ajudar. Oferecemos orientação sólida e estratégica para garantir que as cláusulas contratuais sejam justas e que você esteja em conformidade com as leis trabalhistas aplicáveis</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Rescisão de Contrato e Demissões</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A rescisão de contrato ocorre quando as partes concordam em encerrar o vínculo empregatício de forma amigável. Isso pode acontecer por diversos motivos, como a conclusão de um contrato a prazo determinado, a aposentadoria do empregado ou acordos mútuos. Geralmente, as condições de rescisão são estabelecidas no contrato de trabalho ou em um acordo de rescisão, e podem incluir detalhes sobre aviso prévio, indenização e pagamento de férias proporcionais.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A demissão, por outro lado, ocorre quando o empregador decide encerrar o contrato de trabalho unilateralmente, sem o consentimento do empregado. Existem diferentes formas de demissão, incluindo demissão sem justa causa, demissão por justa causa ou demissão com justa causa</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Advogados especializados em direito trabalhista desempenham um papel crucial ao representar os interesses de ambas as partes em casos de rescisão de contrato e demissões. Eles podem garantir que o processo seja conduzido de acordo com a lei e que os direitos dos envolvidos sejam respeitados, proporcionando orientação e soluções legais em casos de disputas ou litígios. Entre em contato conosco, podemos lhe ajudar!</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Férias e Licenças</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>As férias são um direito assegurado à maioria dos trabalhadores em muitos países, incluindo o Brasil. É importante destacar que as férias devem ser remuneradas com um acréscimo de um terço sobre o salário normal, conhecido como "terço constitucional", para garantir que o trabalhador possa desfrutar plenamente de seu período de descanso. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Licenças são períodos de afastamento do trabalho concedidos aos trabalhadores em situações específicas. Inclui Licença-Maternidade e Licença-Paternidade, Licença por Motivos de Saúde, Licença para Estudo e Aperfeiçoamento Profissional, Licença por Motivos de Casamento ou Luto.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Advogados desempenham um papel crucial em questões de férias e licenças no ambiente de trabalho. Uma das principais razões é a complexidade das leis trabalhistas, que podem variar significativamente de uma jurisdição para outra. Estamos à sua disposição para resolver disputas e garantir que as leis trabalhistas sejam aplicadas adequadamente.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Segurança e Saúde Ocupacional (SSO)</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'> Segurança e Saúde Ocupacional (SSO) é uma área crucial no ambiente de trabalho, visando à proteção da saúde e segurança dos trabalhadores. Ela envolve a implementação de normas e regulamentos que visam prevenir acidentes e doenças ocupacionais, bem como garantir um ambiente de trabalho seguro. Isso inclui a avaliação de riscos, a implementação de medidas preventivas, o fornecimento de equipamentos de proteção, treinamento em segurança e o acompanhamento constante das condições de trabalho.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Além de cumprir com as regulamentações legais, investir na SSO traz benefícios significativos para as empresas, como a redução de custos com acidentes de trabalho, o aumento da produtividade e a melhoria da reputação da empresa. Também contribui para o bem-estar dos funcionários, promovendo uma cultura de segurança e saúde no ambiente laboral.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Compreendemos a importância da Segurança e Saúde Ocupacional (SSO) para a proteção dos trabalhadores e o cumprimento das normas regulamentares. Se sua empresa enfrenta desafios relacionados à SSO, estamos aqui para fornecer a orientação legal necessária.</h1>
        </div>
        
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Terceirização e Trabalho Temporário</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>A terceirização ocorre quando uma empresa contrata outra empresa ou prestador de serviços para realizar atividades específicas que normalmente seriam executadas internamente. Isso pode envolver a terceirização de funções como serviços de TI, limpeza, segurança, contabilidade, entre outros. A terceirização pode ser vantajosa para empresas que desejam focar em suas atividades principais, reduzir custos ou acessar especialização externa.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O trabalho temporário envolve a contratação de funcionários por um período de tempo limitado para atender a demandas sazonais ou específicas de projetos. As empresas contratam trabalhadores temporários por meio de agências de emprego temporário. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Contratar uma empresa de advocacia para auxiliar com a terceirização da sua empresa é fundamental devido à complexidade legal envolvida, à necessidade de elaborar contratos sólidos e garantir a conformidade legal, além de proteger os direitos dos trabalhadores terceirizados. Isso é essencial para o sucesso e a segurança do processo de terceirização.</h1>
        </div>
        <div className='bg-color-white py-12 px-6 md:px-24'>
            <h1 className='text-color-clicado font-bold text-4xl'>Assédio e Discriminação</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>O assédio no trabalho ocorre quando um indivíduo é submetido a comportamentos ofensivos, humilhantes ou hostis por parte de colegas de trabalho, supervisores ou gerentes. Isso pode se manifestar de várias formas, incluindo assédio sexual, assédio moral, assédio racial, assédio religioso, entre outros. </h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Assim como o assédio, a discriminação ocorre quando um funcionário é tratado de forma injusta com base em características pessoais, como raça, gênero, idade, religião, orientação sexual, deficiência, nacionalidade ou outras características protegidas pela lei. Isso pode se manifestar em práticas de contratação, promoção, demissão, remuneração desigual, oportunidades de treinamento e muito mais.</h1>
            <h1 className='text-color-black max-w-[800px] mt-8'>Entendemos como o assédio e a discriminação são assuntos sensíveis e que nem todos se sentem confortáveis para conversar sobre. Saiba que estamos aqui para lhe ajudar e ser sua voz para garantir que a justiça seja feita!  Não hesite em nos contatar para uma consulta confidencial e sem compromisso.</h1>
        </div>
        
    </div>
  )
}
